main {
  position: relative;
  width: 100vw;
  max-width: 100%;
  overflow: hidden;
}

.page-title {
  font-size: 45px;
  line-height: 60px;
  color: black;
  letter-spacing: -0.03em;
}
.section-title {
  font-size: 30px;
  line-height: 50px;
  color: black;
  letter-spacing: -0.03em;
}
.page-text,
.section-text {
  font-size: 20px;
  color: var(--color-secondary);
  line-height: 40px;
  letter-spacing: -0.03em;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 1920px) {
  .container {
    max-width: calc(100% - 480px);
  }
}
@media (max-width: 1440px) {
  .container {
    max-width: calc(100% - 240px);
  }
}
@media (max-width: 1024px) {
  .container {
    max-width: calc(100% - 124px);
  }
  .page-title {
    font-size: 36px;
    line-height: 50px;
  }
  .page-text,
  .section-text {
    font-size: 16px;
    line-height: 30px;
  }
  .section-title {
    font-size: 26px;
    line-height: 40px;
  }
}
@media (max-width: 425px) {
  .container {
    max-width: calc(100% - 40px);
  }
  .page-title {
    font-size: 24px;
    line-height: 36px;
  }
  .section-title {
    font-size: 21px;
    line-height: 34px;
  }
}

.row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(50%, 1fr));
  margin: 125px auto;
}
.col {
  position: relative;
  grid-column: span 1;
  padding-left: 40px;
  padding-right: 40px;
}
.col:first-child {
  padding-left: 0;
}
.col:last-child {
  padding-right: 0;
}
@media (max-width: 768px) {
  .row {
    grid-template-columns: 1fr;
    margin: 30px auto;
  }
  .row.swap-on-mobile .col:last-child {
    grid-row: 1 / 2;
  }
  .row.swap-on-mobile .col:first-child {
    grid-row: 2 / 3;
  }
  .col {
    padding: 0;
    margin: 20px 0;
  }
}
.center-vertical {
  display: flex;
  align-items: center;
}

.cta-form {
  display: flex;
  margin-top: 50px;
  max-width: 100%;
  z-index: 3;
}
.cta-form_input {
  margin: 0;
  height: 50px;
  max-width: 50%;
  min-width: 0;
  background: #ffffff;
  border: 1px solid rgba(72, 72, 81, 0.2);
  box-sizing: border-box;
  border-top-left-radius: 40px;
  border-bottom-left-radius: 40px;
  padding: 16px 25px 14px 25px;
  border-right: 0;
  font-size: 16px;
  line-height: 17px;
  display: inline-flex;
  align-items: center;
  letter-spacing: -0.03em;
  color: #484851;
}
.cta-form_input:focus {
  outline: none;
}
.cta-form_button {
  padding: 16px 25px 14px 25px;
  margin: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  font-family: "Codec Cold Trial";
  font-size: 16px;
  line-height: 17px;
  letter-spacing: -0.03em;
  color: white;
  background: #484851;
  border: 0;
  border-top-right-radius: 40px;
  border-bottom-right-radius: 40px;
  cursor: pointer;
}

.i-like-it-btn {
  font-family: "Codec Cold Trial";
  padding: 0;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  border: 0;
  background-color: transparent;
  font-size: 16px;
  line-height: 26px;
  letter-spacing: -0.03em;
  color: #484851;
}
.i-like-it-btn_icon {
  width: 24px;
  height: 22px;
  margin-right: 17px;
  background-image: url("../assets/images/icon-heart.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: transform 0.2s ease-in-out;
}
.i-like-it-btn:focus {
  outline: none;
}
.i-like-it-btn:hover .i-like-it-btn_icon,
.i-like-it-btn:focus .i-like-it-btn_icon {
  transform: scale(1.1);
}
.i-like-it-btn_icon__filled,
.i-like-it-btn[data-liked="true"] .i-like-it-btn_icon {
  background-image: url("../assets/images/icon-heart_filled.svg");
}
